.mashape-footer {
    font-family: 'Whitney SSm A', 'Whitney SSm B', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 13px;
    line-height: 21px
}

.mashape-footer a {
    text-decoration: none;
    transition: all .3s ease
}

.mashape-footer-container {
    max-width: 1020px;
    width: 100%;
    margin-left: auto;
    margin-right: auto
}

.mashape-footer-bar {
    font-family: 'Whitney SSm SmallCaps A', 'Whitney SSm SmallCaps B', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    padding: 11px 0;
    border-top: 1px solid transparent;
    text-transform: lowercase;
}

.mashape-footer-bar a {
    margin-right: 1em
}

.mashape-footer-bar .mashape-footer-separator {
    display: inline-block;
    height: 20px;
    opacity: .6;
    width: 1px;
    vertical-align: middle;
    margin-right: 1em;
}

.mashape-footer-social {
    list-style: none;
    margin: 0;
    padding: 0;
    float: right
}

.mashape-footer-social:after,
.mashape-footer-social:before {
    content: " ";
    display: table
}

.mashape-footer-social:after {
    clear: both
}

.mashape-footer-social li {
    margin: 0 20px 0 0;
    float: left
}

.mashape-footer-social a {
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: 0 0;
    display: block;
    width: 16px;
    height: 16px;
    margin: 0;
    opacity: .7;
    background-color: transparent;
    border: 0;
    overflow: hidden
}

.mashape-footer-social a:before {
    content: "";
    display: block;
    width: 0;
    height: 150%
}

.mashape-footer-social a:hover {
    opacity: 1
}

.mashape-footer-social a.mf-ico-facebook {
    background-position: -10px -10px;
    width: 7px;
    height: 15px
}

.mashape-footer-social a.mf-ico-twitter {
    background-position: -10px -45px;
    width: 15px;
    height: 13px
}

.mashape-footer-social a.mf-ico-google {
    background-position: -10px -78px;
    width: 15px;
    height: 15px
}

.mashape-footer-social a.mf-ico-github {
    background-position: -10px -113px;
    width: 16px;
    height: 16px
}

.mashape-footer-theme-light a {
    color: #adc7d9
}

.mashape-footer-theme-light a:hover {
    color: #fff
}

.mashape-footer-theme-light .mashape-footer-bar {
    background-color: #f2f4f6;
    color: #74828b;
    border-top-color: #e9ecee
}

.mashape-footer-theme-light .mashape-footer-bar a {
    color: #74828b;
}

.mashape-footer-theme-light .mashape-footer-bar a:hover {
    color: #4a5259;
}

.mashape-footer-theme-light .mashape-footer-bar .mashape-footer-separator {
    background-color: #d9dde1;
}

@media (max-width: 550px) {
    .mashape-footer-bar .mashape-footer-separator,
    .mashape-footer-bar .container > a {
        display: none;
    }
}
